/* Join Room */
.join {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 400px;
  gap: 10px;
  padding: 40px 20px;
  border-radius: 10px;
  box-shadow: 0 2px 8px 2px rgb(164, 165, 168);
}

.join input,
select,
button {
  height: 45px;
  border: none;
  outline: none;
  color: rgb(59, 60, 62);
  border-radius: 4px;
  border: 1px solid rgb(182, 184, 190);
  font-size: 16px;
}

.join input::placeholder,
select::placeholder {
  color: hsl(223, 3%, 56%);
}

.join input,
select {
  padding: 0 10px;
}

/* .join button:hover {
  background-color: rgba(0, 0, 0, 0.5);
} */

.join button:active {
  background-color: rgb(161, 163, 170);
}

/* ChatNdParticipants */
.rightBox {
  border-radius: 10px;
  width: 30%;
  background-color: rgb(54, 54, 54);
  min-width: 250px;
}

.rightBox__optionView {
  padding-top: 5px;
  padding-bottom: 10px;

  height: calc(100% - 40px);
}

.rightBox__head {
  display: flex;
  justify-content: space-around;
  width: 100%;
  padding: 10px 0;
  height: fit-content;
  border-bottom: 1px solid #4b4b4b;
}

.rightBox__head .selected {
  color: black;
}

.rightBox__head span {
  font-weight: bold;
  color: #a6a8ab;
  cursor: pointer;
}

.rightBox__head span:nth-child(1) {
  margin-right: 10px;
}

.rightBox__chats {
  height: calc(100% - 50px);
  margin-bottom: 10px;
  overflow: scroll;
  ms-overflow-style: none;
  scrollbar-width: none; /* Firefox */
}

.rightBox__chats::-webkit-scrollbar {
  display: none;
}

.rightBox__optionView form input {
  border: none;
  width: 100%;
  min-height: 40px;
  background-color: inherit;
  border-radius: 10px;
  padding: 0 10px;
  padding-right: 35px;
}

.rightBox__optionView form input:focus {
  outline: none;
}

.rightBox__participants {
  padding: 10px;
  height: 100%;
  overflow: scroll;
  ms-overflow-style: none;
  scrollbar-width: none; /* Firefox */
}

.rightBox__participants::-webkit-scrollbar {
  display: none;
}

.rightBox__participant {
  text-align: left;
  text-align: left;
  background-color: rgba(221, 221, 221, 0.5);
  padding: 10px;
  border-radius: 10px;
  margin-bottom: 10px;
}

.rightBox__participant p {
  font-size: 14px;
}

/* Controls */
.controls {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  padding: 20px 0;
  height: 80px;
  margin-top: 10px;
  background-color: rgb(54, 54, 54);
  border-radius: 10px;
  gap: 10px;
}

.controls .MuiButton-root .MuiSvgIcon-root {
  margin-right: 5px;
}

.controls .MuiButton-root img {
  height: 20px;
  width: 20px;
  margin-left: 5px;
}

.controls .MuiButton-root {
  text-transform: unset;
  padding: 10px;
  border-radius: 10px;
}

.controls .leave {
  background-color: rgb(241, 110, 110);
}

.controls .leave:hover {
  background-color: rgb(242, 87, 87);
}

/* HlsView and VideoTile */
video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}

.video {
  width: calc(50% - 10px);
  height: unset;
}

/* Message */
.message {
  width: 100%;
  padding: 0 10px;
  text-align: left;
  color: white;
}

.message:hover {
  background-color: rgba(0, 0, 0, 0.5);
}

.message span {
  color: pink;
}

.message p {
  padding: 2px 4px;
  border-radius: 10px;
  font-size: 14px;
  overflow: hidden;
  word-wrap: break-word;
}

/* Room */
.room {
  display: flex;
  width: 95vw;
  height: 90vh;
  border-radius: 10px;
  gap: 10px;
}

.room__streamSpace {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.liveStream {
  border-radius: 10px;
  height: calc(100% - 90px);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  overflow: hidden;
}

/* Stream */
.stream {
  border-radius: 10px;
  /* height: calc(100% - 90px); */
  max-width: 1600px;
  max-height: 900px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  flex-wrap: wrap;
  overflow: hidden;
  background-color: rgb(54, 54, 54);
}

.messageSend {
  position: relative;
  border-radius: 10px;
  background-color: rgba(221, 221, 221, 0.5);
}

.emotes {
  right: 6px;
  top: 6px;
  position: absolute;
  background-color: transparent;
  height: 25px;
  width: 25px;
  line-height: 0;
  cursor: pointer;
  color: rgba(0, 0, 0, 0.3);
}

.emotes:hover {
  background-color: rgba(0, 0, 0, 0.3);
  color: rgba(0, 0, 0, 0.5);
}

.emoteField {
  position: absolute;
  top: -440px;
  right: 0;
}

.emote {
  transform: translateY(4px);
}

.iconBtn {
  color: #cccccc;
}

@media screen and (min-width: 480px) and (max-width: 768px) {
  .room__streamSpace {
    max-height: 60%;
  }
}

@media screen and (max-width: 768px) {
  .room {
    flex-direction: column;
  }

  .room__streamSpace {
    justify-content: start;
  }

  .rightBox {
    width: 100%;
    height: 20%;
    flex-grow: 1;
  }

  .rightBox__participants {
    display: flex;
    flex-wrap: wrap;
  }

  .rightBox__participant {
    height: 60px;
    flex: 0.25;
  }

  .messageSend {
    margin: 0 auto;
    max-width: 480px;
  }
}
